body {
    margin: 0;
    padding: 0;
    font-family: default;
}

.popup {
    border: 4px solid;
    top: 50%;
    left: 50%;
    position: absolute;
    text-align: center;
    padding: 2% 5% 5% 5%;
    transform: translate(-50%, -50%);
    border: 1px solid #D434FE;
    z-index: 4;
}

.pop {
    width: 300%;
    height: 100vh;
    top: 78%;
    left: 0%;
    transform: translate(-50%, -50%);
    position: absolute;
    text-align: center;
    margin-top: -35%;
    background-color: rgba(97, 92, 92, 0.7);
    z-index: 3;
}

.popup img {
    width: 26.6875rem;
    height: 20.0625rem;
}

.popup h2 {
    color: #FFF;
    text-align: center;
    font-size: 2rem;
    font-weight: 600;
    margin-top: -2%;
}

.popup h5 {
    color: #FFF;
    text-align: center;
    font-size: 0.875rem;
    font-weight: 600;
}

.popup button {
    margin-left: -40%;
    outline: none;
}
.popup button {
    width: 80%;
    height: 8%;
    position: absolute;
    color: #FFF;
    font-size: 16px;
    font-weight: 400;
    border-radius: 4px;
    margin-top: 2%;
    cursor: pointer;
    border: transparent;
    outline: none;
    z-index: 2;
    background: linear-gradient(270deg, #903AFF 0%, #D434FE 56.42%, #FF26B9 99.99%, #FE34B9 100%);
}
@media screen and (max-width: 500px) {
    .pop {
        width: 300%;
        height: 135vh;
        margin-top: -26rem;
    }
    .popup {
        padding: 8% 5% 16% 5%;
    }
}