body {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

a {
    text-decoration: none;
}

.contian {
    width: 100%;
    height: 224vh;
    background-color: #150E28;
}

.contents {
    margin-left: 8%;
}

.firsts-part01,
.seconds-part02,
.thirds-part03,
.fourths-part04,
.fifths-part05,
.sixths-part06 {
    padding: 1%;
}

.firsts-part01 {
    width: 25%;
    height: 55%;
    background: #181818dd;
    position: absolute;
    border: 1px solid red;
    margin-top: 140px;
    border-radius: 8px;
    cursor: pointer;
}

.firsts-part01 h1 {
    height: 50%;
    margin-top: 0%;
    background-color: #38256d;
    margin-left: 0%;
    padding-left: 14%;
    font-style: normal;
    border-radius: 5px;
    font-size: 50px;
    color: transparent;
    -webkit-text-stroke: 1px white;
    font-size: 8rem;
}

.firsts-part01:hover {
    margin-top: 9%;
    transition: 400ms;
    box-shadow: 0 0 10px 0 #929191;
}

.firsts-part01 p {
    text-align: center;
    color: #fff;
    margin-top: -60px;
    text-transform: uppercase;
    font-weight: 800;
    line-height: 20px;
}

.firsts-part01 button {
    width: 120px;
    height: 40px;
    color: #FFF;
    font-size: 16px;
    font-weight: 400;
    border-radius: 4px;
    margin-left: 10%;
    margin-top: 8%;
    cursor: pointer;
    border: transparent;
    background: linear-gradient(270deg, #903AFF 0%, #D434FE 56.42%, #FF26B9 99.99%, #FE34B9 100%);
}

.firsts-part01 button:hover {
    background: transparent;
    color: red;
    border: 1px solid red;
}

