.hre span hr {
    width: 87%;
    position: absolute;
    margin-top: 75%;
    margin-left: 0%;
    border: 1px solid #434242;
}

.contacts-us00 h1 {
    text-align: center;
    left: 0%;
    top: 0%;
    right: 0%;
}

.contacts-us01 {
    position: absolute;
    margin-top: 1030px;
}

.hres hr {
    width: 20%;
    margin-left: 0%;
    margin-top: 1095px;
    position: absolute;
    border: 1px solid #3b3b3b;
}

.contacts-us01 h1 {
