}

@media screen and (max-width: 500px) {

    .firsts-part01,
    .seconds-part02,
    .thirds-part03,
    .fourths-part04,
    .fifths-part05,
    .sixths-part06 {
        height: 60%;
        padding: 1%;
        position: absolute;
    }

    .hre span hr {
        display: none;
    }

    .firsts-part01 h1,
    .seconds-part02 h1,
    .thirds-part03 h1,
    .fourths-part04 h1,
    .fifths-part05 h1,
    .sixths-part06 h1 {
        font-size: 65px;
        width: 98.50%;
        height: 40%;
        line-height: 58px;
        padding-left: 2px;
        margin-left: 0%;
        text-align: center;
    }

    .firsts-part01 p,
    .seconds-part02 p,
    .thirds-part03 p,
    .fourths-part04 p,
    .fifths-part05 p,
    .sixths-part06 p {
        font-size: 14px;
        margin-top: -29%;
        font-weight: 200;
    }

    .firsts-part01 button,
    .seconds-part02 button,
    .thirds-part03 button,
    .fourths-part04 button,
    .fifths-part05 button,
    .sixths-part06 button {
        width: 80%;
        height: 10%;
        margin-top: -5%;
        margin-bottom: 10%;
    }

    .fourths-part04 p {
        width: auto;
    }

    .firsts-part01:hover,
    .seconds-part02:hover,
    .thirds-part03:hover {
        margin-top: 26%;
    }

    .fourths-part04:hover:hover,
    .fifths-part05:hover:hover,
    .sixths-part06:hover:hover {
        margin-top: 120%;
    }

    .contacts-us01 textarea {
        width: 200%;
        margin-left: 0%;
    }

    .contacts-us01 h1 {
        font-size: 25px;
        margin-top: -2%;
        margin-bottom: 8%;
    }

    .hres hr {
        margin-top: 1060px;
    }

    .contacts-us01 button {
        margin-top: -4%;
    }
}