body {
    margin: 0;
    padding: 0;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
        'Open Sans', 'Helvetica Neue', sans-serif;
}

.login-body {
    width: 100%;
    height: 100vh;
    background: #150E28;
}

.login-table {
    width: 30%;
    height: 70%;
    padding-left: 4%;
    padding-top: 1%;
    padding-right: 4%;
    margin-left: 51%; 
    position: absolute;
    margin-top: 10%;
    border-radius: 10px;
    box-shadow: 0 0 5px solid black;
    background-color: #342c4a;
}

.login-table h1 {
    color: #D434FE;
    font-weight: 400;
    font-size: 18px;
}

.login-table h2 {
    color: #D434FE;
    font-weight: 400;
    font-size: 18px;
    font-size: 15px;
    margin-top: -7%;
    padding-bottom: 1rem;
}

.login-table input {
    width: 100%;
    outline: none;
    color: #fff;
    padding: 2% 1% 2% 4%;
    margin-bottom: 2rem;
    border: 1px solid #fff;
    background: transparent;
}

.login-table textarea {
    width: 101%;
    height: 20%;
    color: #fff;
    outline: none;
    padding: 2% 0% 2% 4%; 
    background: transparent;
    border: 1px solid #fff;
    margin-top: -4%;
    position: relative;
    z-index: 3;
}

.login-table ::placeholder {
    color: #fff;
    margin-left: 2%;
}

.login-table button {
    width: 25%;
    height: 10%;
    position: absolute;
    color: #FFF;
    font-size: 16px;
    font-weight: 400;
    border-radius: 4px;
    margin-left: 25%;
    margin-top: 6%;
    cursor: pointer;
    border: transparent;
    z-index: 2;
    background: linear-gradient(270deg, #903AFF 0%, #D434FE 56.42%, #FF26B9 99.99%, #FE34B9 100%);
}

.texts6 h1 {
    color: #FF26B9;
}

.texts6 {
    color: #fff;
    position: absolute;
    margin-left: 14%;
    margin-top: 14%;
    line-height: 18px;
}

.join2 {
    display: inline;
    margin-top: -1%;
    line-height: 2px;
    position: absolute;
    width: 60%;
    margin-left: -2%;
}

.join2 p {
    color: #FF26B9;
    z-index: 3;
}

.join2 a {
    padding-right: 7%;
    z-index: 3;
    position: relative;
}

.join2 .insta {
    margin-left: -8%;
    z-index: 3;
}

.flame12 {
    width: 40rem;
    margin-top: -70%;
    margin-left: -30%;
    rotate: -90deg;
    z-index: 1;
}

.flames10 img {
    width: 40rem;
    height: 50%;
    opacity: 50%;
    margin-top: -20%;
    margin-left: 40%;
    z-index: 1;
}

.star01 {
    opacity: 40%;
    margin-left: -70;
    margin-top: -75%;
    margin-left: 80%;
    width: 0.798rem;
    position: absolute;
}

.star03 {
    margin-left: -70;
    margin-top: -75%;
    margin-left: -110%;
    width: 0.798rem;
    position: absolute;
}

.star04 {
    margin-left: -70;
    margin-top: -10%;
    margin-left: -13%;
    width: 1.30rem;
    position: absolute;
}

.star02 {
    margin-left: -70;
    margin-top: 18%;
    margin-left: 100%;
    width: 1.30rem;
    position: absolute;
}

.topic {
    display: none; 
}

.login-table p {
    display: none;
}

@media screen and (max-width: 780px) {
    .login-body {
        width: 100%;
        height: 100vh;
        background: #150E28;
    }

    .topic {
        display: block;
    }

    .texts6 h1,
    .p1,
    .p2,
    .p3,
    .p4 {
        visibility: hidden;
    }

    .login-table {
        background: transparent;
    }

    .login-table h1 {
        width: 200%;
        margin-left: -200%;
        margin-top: 2rem;
    }

    .login-table h2 {
        width: 150%;
        margin-left: -200%;
        margin-top: -28%;
        margin-bottom: 30%;
    }

    .login-table p {
        display: block;
        width: 250%;
        color: #fff;
        margin-left: -200%;
        margin-top: -20%;
        margin-bottom: 25%;
    }

    .login-table form input,
    textarea {
        margin-left: -200%;
        width: 350%;
        padding: 5% 5% 5% 10%;
        margin-bottom: 18%;
    }

    .login-table textarea {
        width: 350%;
        margin-top: -18%;
        padding: 5% 5% 5% 10%;
    }

    .login-table button {
        width: 100%;
        margin-left: -72%;
        margin-top: -4%;
    }

    .join2 {
        line-height: 1%;
        margin-top: 15.30rem;
        margin-left: 60%;
    }

    .flames10 img {
        width: 22rem;
        height: 12.60rem;
        margin-top: -30rem;
        margin-left: -22rem;
        position: absolute;
        z-index: 1;
    }

    .flame12 {
        visibility: hidden;
    }
    .star03 {
        margin-left: 17%;
        margin-top: -28rem;
    }
    .star01 {
        margin-left: -160%;
        margin-top: -16rem;
    }
    .star02 {
        width: 0.880rem;
        margin-top: -28%;
        margin-left: 120%;
    }
    .star04 {
        width: 0.880rem;
        margin-top: 8%;
        margin-left: -170%;
    }
}