body {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: default;
}

html {
    font-size: 15px;
}

a {
    cursor: pointer;
}

.navbar {
    width: 100%;
    background: #150E28;
    position: absolute;
}

.nav-2 {
    display: inline;
}

.navbar h3 {
    margin-left: 6.60rem;
    display: inline;
}

.navbar h3 a {
    position: absolute;
    font-size: 36px;
    margin-top: 1rem;
    display: inline;
    color: #fff;
    text-decoration: none;
    z-index: 2;
}

.navbar h3 a p {
    display: inline;
    color: #FF26B9;
    margin-right: 20rem;
    font-size: 36px;
}

.nav-2 li {
    list-style: none;
}

.nav-2 a {
    text-decoration: none;
    color: #FFF;
    font-size: 16px;
    margin-left: 80px;
    margin-right: -1.50%;
    font-weight: 400;
}

.nav-2 a:hover {
    color: #D434FE;
}

.nav-2 .contact {
    margin-right: 60px;
}

.nav-2 .merge {
    margin-left: 49%;
}

.nav-2 a button {
    width: 120px;
    height: 40px;
    color: #FFF;
    font-size: 16px;
    font-weight: 400;
    border-radius: 4px;
    margin-left: -5%;
    cursor: pointer;
    border: transparent;
    background: linear-gradient(270deg, #903AFF 0%, #D434FE 56.42%, #FF26B9 99.99%, #FE34B9 100%);
}

.nav-2 a button:hover {
    background: transparent;
    border: 1px solid #903AFF;
}

hr {
    margin-top: 1%;
    opacity: 0.20;
}

.img5 {
    display: none;
    position: absolute;
}

.hamburger {
    display: none;
}

.sidebar {
    display: none;
}

@media screen and (max-width: 780px) {
    .navbar {
        width: 100%;
        height: 10%;
        background: #150E28;
    }

    .navbar h3 a {
        font-size: 0.9375rem;
        position: absolute;
        margin-left: -3rem;
        margin-top: 4%;
        text-decoration: none;
    }

    .navbar h3 a p {
        margin-top: 4%;
        font-size: 0.9375rem;
    }

    .navbar hr {
        margin-top: 7%;
    }

    .nav-2 a {
        display: none;
    }

    .nav-2 button {
        display: none;
    }

    /* menue-part */
    *,
    *::before,
    *::after {
        box-sizing: border-box;
    }

    body {
        margin: 0;
    }

    :root {
        --bar-width: 35px;
        --bar-height: 3px;
        --hamburger-gap: 6px;
        --forebackground: #fff;
        --background2: #150E28;
        --hamburger-margin: 8px;
        --animation-timing: 200ms ease-in-out;
        --hamburger-height: calc(var(--bar-height) * 3 + var(--hamburger-gap) * 2);

    }

    .hamburger {
        display: flex;
        flex-direction: column;
        gap: var(--hamburger-gap);
        width: max-content;
        top: 18px;
        left: 25.60rem;
        position: absolute;
        z-index: 5;
        cursor: pointer;
        --x-width: calc(var(--hamburger-height) * 1.41421356237);
    }

    .hamburger::before,
    .hamburger::after,
    .hamburger input {
        content: "";
        width: var(--bar-width);
        height: var(--bar-height);
        background-color: var(--forebackground);
        border-radius: 9999px;
        transform-origin: left center;
        transition: opacity var(--animation-timing),
            width var(--animation-timing), rotate var(--animation-timing),
            translate var(--animation-timing), background-color var(--animation-timing);
    }

    .hamburger input {
        appearance: none;
        padding: 0;
        margin: 0;
        outline: none;
        pointer-events: none;
    }

    .hamburger:has(input:checked)::before {
        rotate: 45deg;
        width: var(--x-width);
        translate: 0 calc(var(--bar-height) / -2);
    }

    .hamburger:has(input:checked)::after {
        rotate: -45deg;
        width: var(--x-width);
        translate: 0 calc(var(--bar-height) / 2);
    }

    .hamburger input:checked {
        opacity: 0;
        width: 0;
    }

    .sidebar {
        display: block;
        transition: translate var(--animation-timing);
        translate: -100%;
        padding-top: 2rem;
        padding-top: calc(var(--hamburger-height) + var(--hamburger-margin) + 1rem);
        background: var(--background2);
        backdrop-filter: blur(30px);
        border: 0.5px solid rgba(255, 255, 255, 0.04);
        color: var(--background);
        width: 32.85rem;
        height: 26.75rem;
        margin-top: -5%;
        position: absolute;
        z-index: 4;
        border-radius: 10px;
    }

    .sidebar a {
        display: flex;
        text-decoration: none;
        color: #fff;
        margin-top: 6%;
        margin-left: 20%;
        font-size: 1.125rem;
    }

    .sidebar a button {
        width: 120px;
        height: 40px;
        color: #FFF;
        font-size: 16px;
        font-weight: 400;
        border-radius: 4px;
        margin-left: 1%;
        cursor: pointer;
        border: transparent;
        background: linear-gradient(270deg, #903AFF 0%, #D434FE 56.42%, #FF26B9 99.99%, #FE34B9 100%);
    }

    .sidebar a:hover {
        color: #FE34B9;
    }

    .sidebar a.active {
        background: linear-gradient(90deg, #903AFF 50%, #FF26B9 50%);
        -webkit-background-clip: text;
        color: transparent;
    }

    .sidebar a button:hover {
        background: transparent;
        border: 2px solid #903AFF;
    }

    .hamburger:has(input:focus-visible)::before,
    .hamburger:has(input:focus-visible)::after,
    .hamburgerinput:focus-visible {
        border: 1px solid var(--background);
        box-shadow: 0 0 0 1px var(--forebackground);
    }

    .hamburger:has(input:checked) {
        --forebackground: white;
        --background: #333;
    }

    .hamburger:has(input:checked)+.sidebar {
        translate: 0;
    }

    .circle {
        width: 2.4375rem;
        height: 2.4375rem;
        position: absolute;
        margin-left: 76.60%;
        margin-top: -6.88%;
    }

}

.nav-2 a.active {
    background: linear-gradient(90deg, #903AFF 50%, #FF26B9 50%);
    -webkit-background-clip: text;
    color: transparent;
}