body {
    margin: 0;
    padding: 0;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
        'Open Sans', 'Helvetica Neue', sans-serif;
}

.signup-table {
    width: 100%;
    height: 90vh;
    background: #150E28;
    justify-content: center;
    margin-top: 63px;
    position: absolute;
}

.information {
    width: 36%;
    height: 92%;
    padding-left: 4%;
    padding-top: 1%;
    padding-right: 2%;
    margin-left: 50%;
    position: absolute;
    margin-top: 2%;
    border-radius: 10px;
    box-shadow: 0 0 5px solid black;
    background-color: #342c4a;
}

.before h1 {
    color: #FF26B9;
}

.before p {
    color: #fff;
    margin-top: -2%;
}

.before h3 {
    color: #fff;
}

.run {
    margin-left: 5%;
    margin-right: -7%;
}

.dotted-line img {
    width: 15%;
    margin-top: -3.50%;
    margin-left: 29%;
    position: absolute;
}

.parts input {
    width: 90%;
    display: block;
    padding: 5% 5% 5% 10%;
    margin-right: 2rem;
    background: transparent;
    outline: none;
    border: 1px solid #fff;
    border-radius: 5px;
    margin-bottom: 1.60rem;
    color: #FFF;
    position: relative;
    z-index: 2;
}

.parts {
    margin-right: 2.50rem;
    display: inline-block;
}

.parts label {
    color: #fff;
}

.parts select {
    background: transparent;
    color: #fff;
    border: 1px solid #fff;
    border-radius: 6px;
    padding: 5% 25% 5% 10%;
    outline: none;
    position: relative;
    z-index: 2;
}

.parts .select2 {
    width: 130%;
    z-index: 2;
    position: relative;
}

.parts option {
    background: #e6e5e5;
    color: #000;
    border-radius: 6px;
    border: 1px solid red;
    z-index: 2;
}

.udertext p {
    color: #FF26B9;
    font-style: italic;
    font-size: 0.75rem;
}

.udertext .check label {
    color: #fff;
    font-size: 0.75rem;
}

.udertext .check {
    margin-top: -0.60rem;
    z-index: 2;
    position: relative;
}

.udertext button {
    width: 80%;
    height: 8%;
    position: absolute;
    color: #FFF;
    font-size: 16px;
    font-weight: 400;
    border-radius: 4px;
    margin-top: 2%;
    cursor: pointer;
    border: transparent;
    outline: none;
    z-index: 2;
    background: linear-gradient(270deg, #903AFF 0%, #D434FE 56.42%, #FF26B9 99.99%, #FE34B9 100%);
}

.sit2 {
    width: 46%;
    height: 100%;
    position: absolute;
    margin-left: 2%;
    z-index: 2;
}

.purpStr {
    width: 0.870rem;
    margin-left: 7.50%;
    margin-top: 5%;
    position: absolute;
}

.purpStr2 {
    width: 0.870rem;
    margin-left: 42%;
    margin-top: 36%;
    position: absolute;
}

.whtStr {
    width: 0.870rem;
    margin-left: 11%;
    margin-top: 40%;
    position: absolute;
}

.whtStr2 {
    width: 0.870rem;
    margin-left: 60%;
    margin-top: -26%;
    position: absolute;
}

.purp {
    width: 40%;
    height: 64%;
    margin-left: -5%;
    position: absolute;
    opacity: 90%;
    rotate: 180deg;
}

.purp2 {
    width: 30%;
    height: 120%;
    margin-left: 23%;
    opacity: 80%;
    margin-top: -28%;
    rotate: 90deg;
    position: fixed;
    z-index: 0;
}

.styled2 {
    display: none;
}

.error-message1 {
    color: red;
    margin-top: -4%;
    padding-bottom: 2%;
    font-style: italic;
    position: absolute;
    z-index: 2;
}

.error-message2 {
    color: red;
    margin-top: -4%;
    position: absolute;
    margin-left: 42.50%;
    font-style: italic;
    z-index: 2;
}

.error-message3 {
    color: red;
    margin-top: -4%;
    position: absolute;
    font-style: italic;
    z-index: 2;
}

.error-message4 {
    color: red;
    margin-top: -4%;
    margin-left: 42.50%;
    position: absolute;
    font-style: italic;
    z-index: 2;
}

@media screen and (max-width: 780px) {
    .signup-table {
        width: 100%;
        height: 130vh;
        background: #150E28;
        justify-content: center;
        margin-top: 2px;
        z-index: 5;
    }

    .information {
        background-color: transparent;
        margin-top: 40%;
    } 

    .sit2 {
        width: 12.1875rem;
        height: 9.6875rem;
        position: absolute;
        margin-left: 30%;
        margin-top: 8%;
        z-index: 2;
    }

    .before p {
        width: 260px;
        margin-left: -140%;
        margin-top: -10%;
    }

    .before h1 {
        color: #D434FE;
        font-size: 0.9375rem;
        font-weight: 700;
        margin-left: -14rem;
        margin-top: -100%;
        position: absolute;
    }

    .dotted-line img {
        width: 40%;
        margin-top: -10%;
        margin-left: -24%;
    }

    .before h3 {
        margin-top: -2%;
        margin-left: -140%;
    }

    .parts input,
    select {
        width: 150%;
        margin-right: 5rem;
    }

    .parts {
        margin-left: -140%;
    }

    .parts .select1 {
        width: 80%;
        padding-top: 4%;
        padding-right: 1%;
        padding-bottom: 4%;
    }

    .parts .select2 {
        width: 70%;
        padding: 7% 2% 7% 5%;
        margin-left: 74%;
        margin-top: -15.60%;
        position: absolute;
    }

    .parts .sect {
        margin-top: -27%;
        margin-left: 74%;
        position: absolute;
    }

    .error-message1,
    .error-message2,
    .error-message3,
    .error-message4 {
        margin-left: -117%;
        margin-top: -13%;
    }

    .udertext p {
        margin-left: -140%;
    }

    .check {
        margin-left: -140%;
    }

    .udertext button {
        width: 74%;
        height: 6%;
        position: absolute;
        color: #FFF;
        font-size: 16px;
        font-weight: 400;
        border-radius: 4px;
        margin-top: 2%;
        cursor: pointer;
        border: transparent;
        outline: none;
        margin-left: -42%;
        margin-top: 7%;
        z-index: 2;
        background: linear-gradient(270deg, #903AFF 0%, #D434FE 56.42%, #FF26B9 99.99%, #FE34B9 100%);
    }
    .purp {
        width: 80%;
        height: 40%;
        margin-left: -25%;
        position: absolute;
        opacity: 60%;
    }
    .purpStr {
        margin-top: 25%;
        margin-left: 75%;
    }
    .purpStr2 {
        margin-top: 124%;
        margin-left: 92%;
    }
    .whtStr {
        margin-top: 58%;
        margin-left: 7.50%;
        opacity: 60%;
    }
    .whtStr2 {
        display: none;
    }
} 