body {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.contianer12 {
    width: 100%;
    height: 100vh;
    background-color: #150E28;
}

.contianer12 h1 {
    position: absolute;
    text-align: center;
    color: #fff;
    margin-left: 26%;
    margin-top: 20%;
    font-size: 3rem; 
    -webkit-text-stroke: 2px #D434FE;
    color: #fff;
}
.contianer12 h1:hover {
    color: #Fff;
}
@media screen and (max-width: 780px) {
    .contianer12 h1 {
        position: absolute;
        text-align: center;
        color: #fff;
        margin-left: 10%;
        margin-top: 50%;
        font-size: 2em;
        -webkit-text-stroke: 1px #D434FE;
        color: #fff;
    }
    .contianer12 h1:hover {
        color: #Fff;
    }
}